<template>
  <section>
    <div class="container">
      <div class="search-wrapper">
        <div class="search-header">
          <div class="back" @click="goBack()">
            <font-awesome-icon icon="arrow-left" />
          </div>
          <div class="search-ribbon">
            <input
              type="text"
              placeholder="Search for jobs and people"
              autofocus
              v-model="query"
            />
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      query: "",
    };
  },
  watch: {
    /**
     * Search job and people
     */
    async query() {
      try {
        const res = await axios.post(
          `https://search.torre.co/people/_search/?${this.query}`
        );
        console.log("res", res);
      } catch (error) {
        return error
      }
    },
  },
  methods: {
    /**
     * Go back to home component
     */
    goBack() {
      document.querySelector(".search-wrapper").style.left = "105%";
      document.querySelector(".search-wrapper").style.display = "none";
      document.querySelector(".page-backdrop").style.left = "105%";
    }
  },
};
</script>